import * as THREE from 'three';

import GlobalVars from "../../data/globalVars";

export default class Waist {

    constructor(scene) {

        const _material = new THREE.MeshStandardMaterial();
        _material.color = new THREE.Color(0x64ede2);
        _material.wireframe = false;

        this.alpha = 0;

        this.obj = new THREE.Object3D();
        //this.obj.add(new THREE.Mesh(new THREE.SphereGeometry(.3, 32, 32), _material));


        /* ----------------------------- */
        /* ----------------------------- */
        /* ----------------------------- */
        this.lowObj = new THREE.Object3D();
        this.lowObj.position.set(0, 0, 0);

        //create waist cube
        const _waistGeometry = new THREE.BoxGeometry(1, 1, 1.3);
        this.waist = new THREE.Mesh( _waistGeometry, _material);
        this.lowObj.add(this.waist);
        this.obj.add(this.lowObj);






        /* ----------------------------- */
        /* ----------------------------- */
        /* ----------------------------- */
        this.upObj = new THREE.Object3D();
        this.obj.add(this.upObj);
        //this.upObj.add(new THREE.Mesh(new THREE.SphereGeometry(.3, 32, 32), _material));
        //this.upObj.position.add(this.lowObj.getWorldPosition(new THREE.Vector3(0, 0.5, 0)));


        //create back pivot
        this._backPivot = new THREE.Object3D();
        this._backPivot.position.set(0, 1/2, 0);
        this.upObj.add(this._backPivot);

        //create back cube
        const _backGeomtry = new THREE.BoxGeometry(1.1, 0.5, 1.3);
        this._back = new THREE.Mesh( _backGeomtry, _material);
        this._back.position.set(0, 0.5/2 + 0.2, 0);
        this._backPivot.add(this._back);



        //create back pivot
        this._backPivot2 = new THREE.Object3D();
        this._backPivot2.position.set(0, (0.6), 0);
        this._backPivot.add(this._backPivot2);

        //create back cube
        const _backGeomtry2 = new THREE.BoxGeometry(1.3, 0.5, 1.5);
        this._back2 = new THREE.Mesh( _backGeomtry2, _material);
        this._back2.position.set(0, 0.5/2 + 0.2, 0);
        this._backPivot2.add(this._back2);



        //create back pivot
        this._backPivot3 = new THREE.Object3D();
        this._backPivot3.position.set(0, (0.6), 0);
        this._backPivot2.add(this._backPivot3);

        //create back cube
        const _backGeomtry3 = new THREE.BoxGeometry(1.5, 0.5, 1.7);
        this._back3 = new THREE.Mesh( _backGeomtry3, _material);
        this._back3.position.set(0, 0.5/2 + 0.2, 0);
        this._backPivot3.add(this._back3);


        //create back pivot
        this._backPivot4 = new THREE.Object3D();
        this._backPivot4.position.set(0, (0.6), 0);
        this._backPivot3.add(this._backPivot4);

        //create back cube
        const _backGeomtry4 = new THREE.BoxGeometry(1.7, 0.5, 1.9);
        this._back4 = new THREE.Mesh( _backGeomtry4, _material);
        this._back4.position.set(0, 0.5/2 + 0.2, 0);
        this._backPivot4.add(this._back4);

        //create back pivot
        this._backPivot5 = new THREE.Object3D();
        this._backPivot5.position.set(0, (0.6), 0);
        this._backPivot4.add(this._backPivot5);

        //create back cube
        const _backGeomtry5 = new THREE.BoxGeometry(1.1, 0.4, 1.5);
        this._back4 = new THREE.Mesh( _backGeomtry5, _material);
        this._back4.position.set(0, 0.4/2, 0);
        this._backPivot5.add(this._back4);

        this._headObj = new THREE.Object3D();
        this._headObj.position.set(0, 0.6, 0);
        //this._headObj.add(new THREE.Mesh(new THREE.SphereGeometry(.3, 32, 32), _material));
        this._backPivot5.add(this._headObj);


        scene.add(this.obj);
    }

    update(){

        const y = 0.3 * Math.sin(2*GlobalVars.t + Math.PI) + this.yy;
        this.obj.position.set(0, y, 0);


        this.lowObj.rotation.y = a * Math.sin(GlobalVars.t); //waist rotation
        this.upObj.rotation.y = a * Math.sin(GlobalVars.t + Math.PI); //shoulder rotation

    }
}

const a = 0.15;
