import * as THREE from 'three';

import GlobalVars from "../../data/globalVars";

import Waist from '../classes/waist';
import Arm from '../classes/arm';
import Leg from '../classes/leg';
import Head from '../classes/head'

export default class Walkman {

    constructor(scene) {

        const dist = 0.8;

        this._waist  = new Waist(scene);
        this._waist.yy = 4;
        this._waist.obj.rotation.z = 10 * Math.PI/180;

        // ----------
        this._leftArm = new Arm(this._waist.upObj, 1);
        this._leftArm.alpha = Math.PI;

        this._rightArm = new Arm(this._waist.upObj, -1);

        this._leftArm.obj.position.add(new THREE.Vector3(0, 3, dist));
        this._rightArm.obj.position.add(new THREE.Vector3(0, 3, -dist));

        // ----------
        this._leftLeg = new Leg(this._waist.lowObj);
        this._leftLeg.d = -1;

        this._rightLeg = new Leg(this._waist.lowObj);
        this._rightLeg.alpha = Math.PI;
        this._rightLeg.d = 1;

        this._leftLeg.obj.position.add(new THREE.Vector3(0, 0, dist));
        this._rightLeg.obj.position.add(new THREE.Vector3(0, 0, -dist));

        // ----------
        this._head = new Head(this._waist._headObj);


    }

    update(){


        this._waist.update();
        //this._waist.obj.rotation.z = GlobalVars.t;

        this._leftLeg.update();
        this._rightLeg.update();

        this._leftArm.update();
        this._rightArm.update();

    }
}
