import * as THREE from 'three';

import GlobalVars from "../../data/globalVars";
import Math2 from "../../data/math2";

export default class Arm {

    constructor(scene, d) {

        const _material = new THREE.MeshStandardMaterial();
        _material.color = new THREE.Color(0x64ede2);
        _material.wireframe = false;

        this.alpha = 0;

        //create container object
        this.obj = new THREE.Object3D();
        //this.obj.position.set(0, 3.75, 0);
        this.obj.rotation.x = -20 * Math2.deg2rad * d;
        this.obj.position.set(0, 0, 0);

        //DEBUG
        //this.obj.add(new THREE.Mesh(new THREE.SphereGeometry(.35, 32, 32), _material));


        //create upper cube
        const _upperArmGeometry = new THREE.BoxGeometry(0.35, 1.5, 0.35);
        this._upperLeg = new THREE.Mesh( _upperArmGeometry, _material);
        this._upperLeg.position.add(new THREE.Vector3(0, -1.5/2, 0));
        this.obj.add(this._upperLeg);

        //create lower pivot
        this.lowPivot = new THREE.Object3D();
        this.lowPivot.position.set(0, -1.5, 0);
        this.lowPivot.rotation.y = -15 * Math2.deg2rad * d;
        this.obj.add(this.lowPivot);

        //create lower cube
        const _lowerLegGeometry = new THREE.BoxGeometry(0.35, 1, 0.35);
        this._lowerLeg = new THREE.Mesh( _lowerLegGeometry, _material);
        this._lowerLeg.position.add(new THREE.Vector3(0, -1/2, 0));
        this.lowPivot.add(this._lowerLeg);

        //create hand pivot
        this.handPivot = new THREE.Object3D();
        this.handPivot.position.set(0, -1, 0);
        this.handPivot.rotation.y = 15*Math2.deg2rad * d;
        this.lowPivot.add(this.handPivot);


        //create finger cube
        const _fingerGeometry = new THREE.BoxGeometry(0.15, .35, 0.15);
        this._finger = new THREE.Mesh( _fingerGeometry, _material);
        this._finger.position.add(new THREE.Vector3(-0.35, -0.25/2, 0));
        this._finger.rotation.z =  -30 * Math2.deg2rad;
        this.handPivot.add(this._finger);

        //create foot cube
        const _handGeometry = new THREE.BoxGeometry(0.4, .75, 0.15);
        this._hand = new THREE.Mesh( _handGeometry, _material);
        this._hand.position.add(new THREE.Vector3(0, -0.25/2, 0));
        this.handPivot.add(this._hand);


        scene.add(this.obj);
    }

    update(){

        //hands
        this.obj.rotation.z = 0.5 * Math.sin(GlobalVars.t + this.alpha);
        this.lowPivot.rotation.z = 0.5 * Math.sin(GlobalVars.t + this.alpha) + (-90 * Math2.deg2rad);

    }
}
