// Global imports -
import * as THREE from 'three';
import TWEEN from '@tweenjs/tween.js';

// Local imports -
// Components
import Renderer from './components/renderer';
import Camera from './components/camera';
import Light from './components/light';
import Controls from './components/controls';
import Geometry from './components/geometry';

import WalkMan from './components/walkman';

// Helpers
import Stats from './helpers/stats';
import MeshHelper from './helpers/meshHelper';

// Model
import Texture from './model/texture';
import Model from './model/model';

// Managers
import Interaction from './managers/interaction';
import DatGUI from './managers/datGUI';

// data
import Config from './../data/config';
import GlobalVars from './../data/globalVars';


// -- End of imports

// This class instantiates and ties all of the components together, starts the loading process and renders the main loop
export default class Main {
    constructor(container) {
        // Set container property to container element
        this.container = container;

        // Start Three clock
        this.clock = new THREE.Clock();

        // Main scene creation
        this.scene = new THREE.Scene();
        this.scene.background = new THREE.Color( 0x1d2329 );
        this.scene.fog = new THREE.FogExp2(Config.fog.color, Config.fog.near);

        // Get Device Pixel Ratio first for retina
        if(window.devicePixelRatio) {
            Config.dpr = window.devicePixelRatio;
        }

        // Main renderer constructor
        this.renderer = new Renderer(this.scene, container);

        // Components instantiations
        this.camera = new Camera(this.renderer.threeRenderer);
        this.controls = new Controls(this.camera.threeCamera, container);
        this.light = new Light(this.scene);

        // Create and place lights in scene
        const lights = ['ambient', 'directional', 'point', 'hemi'];
        lights.forEach((light) => this.light.place(light));

        // Create and place geo in scene
        this.geometry = new Geometry(this.scene);
        this.geometry.make('plane')(20, 20, 10, 10);
        this.geometry.place([0, 0, 0], [Math.PI / 2, 0, 0]);


        // Set up rStats if dev environment
        if(Config.isDev && Config.isShowingStats) {
            this.stats = new Stats(this.renderer);
            this.stats.setUp();
        }

        // Set up gui
        if (Config.isDev) {
            this.gui = new DatGUI(this)
            Config.isLoaded = true;
            this.container.querySelector('#loading').style.display = 'none';
        }

        this.man = new THREE.Object3D();
        this.man_chido = new WalkMan(this.man);

        this.man.position.set(0, 0, 0);
        //this.man.add(this.man_chido);

        this.container = new THREE.Object3D();
        this.container.add(this.man);

        this.scene.add(this.man);


        /*const _donutGeo = new THREE.TorusGeometry( 25.5, 6, 16, 100 );
        const material = new THREE.MeshBasicMaterial( { color: 0xffff00 } );
        const donut = new THREE.Mesh( _donutGeo, material );
        this.scene.add(donut);*/

        // Start render which does not wait for model fully loaded
        this.render();
    }

    render() {
        // Render rStats if Dev
        if(Config.isDev && Config.isShowingStats) {
            Stats.start();
        }

        // Call render function and pass in created scene and camera
        this.renderer.render(this.scene, this.camera.threeCamera);

        // rStats has finished determining render call now
        if(Config.isDev && Config.isShowingStats) {
            Stats.end();
        }

        // Delta time is sometimes needed for certain updates
        //const delta = this.clock.getDelta();

        //this.container.rotation.z = -GlobalVars.t/6;
        this.man_chido.update();

        GlobalVars.t+= 0.125;

        // Call any vendor or module frame updates here
        TWEEN.update();
        this.controls.threeControls.update();


        // RAF
        requestAnimationFrame(this.render.bind(this)); // Bind the main class instead of window object
    }
}
