import * as THREE from 'three';

import GlobalVars from "../../data/globalVars";

export default class Head {

    constructor(scene) {

        const _material = new THREE.MeshStandardMaterial();
        _material.color = new THREE.Color(0x64ede2);
        _material.wireframe = false;

        this.alpha = 0;

        this.obj = new THREE.Object3D();

        const _headGeometry = new THREE.SphereGeometry(0.75, 32, 32);
        this._head = new THREE.Mesh( _headGeometry, _material)
        this._head.scale.set(1, 1.1, 0.8);
        this._head.position.set(0, 0.5, 0);
        this.obj.add(this._head);




        scene.add(this.obj);
    }

    update(){


    }
}
