import * as THREE from 'three';

import GlobalVars from "../../data/globalVars";
import Math2 from "../../data/math2";

export default class Leg {

    constructor(scene) {

        const _material = new THREE.MeshStandardMaterial();
        _material.color = new THREE.Color(0x64ede2);


        this.alpha = 0;

        //create container object
        this.obj = new THREE.Object3D();
        //this.obj.position.set(0, 3.75, 0);
        this.obj.position.set(0, 0, 0);

        //DEBUG
        //this.obj.add(new THREE.Mesh(new THREE.SphereGeometry(0.2, 32, 32), _material));


        //create upper cube
        const _upperLegGeometry = new THREE.BoxGeometry(0.5, 2, 0.5);
        this._upperLeg = new THREE.Mesh( _upperLegGeometry, _material);
        this._upperLeg.position.add(new THREE.Vector3(0, -1, 0));
        this.obj.add(this._upperLeg);

        //create lower pivot
        this.lowPivot = new THREE.Object3D();
        this.lowPivot.position.set(0, -2, 0);
        this.obj.add(this.lowPivot);

        //create lower cube
        const _lowerLegGeometry = new THREE.BoxGeometry(0.5, 1.5, 0.5);
        this._lowerLeg = new THREE.Mesh( _lowerLegGeometry, _material);
        this._lowerLeg.position.add(new THREE.Vector3(0, -1.5/2, 0));
        this.lowPivot.add(this._lowerLeg);

        //create foot pivot
        this.footPivot = new THREE.Object3D();
        this.footPivot.position.set(0, -1.5, 0);
        this.lowPivot.add(this.footPivot);

        //create foot cube
        const _footGeometry = new THREE.BoxGeometry(1, 0.25, 0.5);
        this._foot = new THREE.Mesh( _footGeometry, _material);
        this._foot.position.add(new THREE.Vector3(-.5 + .25, -0.25/2, 0));
        this.footPivot.add(this._foot);


        scene.add(this.obj);
    }

    update(){


        this.obj.rotation.z = 1 * Math.sin(GlobalVars.t + this.alpha);
        this.lowPivot.rotation.z = 0.75 * Math.sin(GlobalVars.t + Math.PI/2 * this.d) + (45*Math2.deg2rad);   //the phase must be the middle of the upper
        this.footPivot.rotation.z = 0.5 * Math.sin(GlobalVars.t + Math.PI/2 * this.d) + (20*Math2.deg2rad);   //the pase must be the middle of the upper



        //hands
        /*this.obj.rotation.z = 0.5 * Math.sin(GlobalVars.t + this.alpha);
        this.lowPivot.rotation.z = 0.5 * Math.sin(GlobalVars.t) + 30;*/

    }
}
